___CSS_LOADER_EXPORT___.push([module.id, ".done{text-decoration:line-through}table{width:100%}table tr:nth-child(2n){background:#f3f4f6}table tr td:first-child{padding-right:20px;white-space:nowrap;width:.1%}table tr td:first-child input[type=checkbox]{margin-right:10px}table i{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Dashboard.vue"],"names":[],"mappings":"AAkFI,MACI,4BACJ,CACA,MACI,UACJ,CACA,uBACI,kBACJ,CACA,wBAGI,kBAAmB,CADnB,kBAAmB,CADnB,SAGJ,CACA,6CACI,iBACJ,CACA,QACI,cACJ","sourcesContent":["<template>\n    <Head title=\"Dashboard\" />\n\n    <BreezeAuthenticatedLayout>\n        <template #header>\n            <h2 class=\"font-semibold text-xl text-gray-800 leading-tight\">\n                Dashboard\n            </h2>\n        </template>\n\n        <div class=\"py-12\">\n            <div class=\"max-w-7xl mx-auto sm:px-6 lg:px-8\">\n                <div class=\"bg-white overflow-hidden shadow-sm sm:rounded-lg\">\n                    <div class=\"p-6 bg-white border-b border-gray-200\">\n                        <h2>Todo<span v-if=\"todos.length > 1\">s</span></h2>\n                        <form @submit.prevent=\"submit\" >\n                            <div id=\"todoInput\">\n                                <Input :name=\"'todo'\" v-model=\"newTodo\" />\n                                <Button>Add</Button>\n                            </div>\n                        </form>\n                        <table>\n                            <tr v-for=\"todo in todos\" :key=\"todo.id\">\n                                <td>\n                                    <label :class=\"!!todo.done && 'done'\">\n                                        <Checkbox @update:checked=\"check\" :value=\"todo.id\" :checked=\"!!todo.done\"/> {{ todo.description }}\n                                    </label>\n                                </td>\n                                <td>\n                                    <i @click=\"remove(todo.id)\" class=\"fas fa-trash-alt\"></i>\n                                </td>\n                            </tr>\n                        </table>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </BreezeAuthenticatedLayout>\n</template>\n\n<script>\nimport BreezeAuthenticatedLayout from '@/Layouts/Authenticated.vue'\nimport { Head } from '@inertiajs/inertia-vue3';\nimport { Inertia } from \"@inertiajs/inertia\";\nimport Checkbox from '@/Components/Checkbox.vue';\nimport Button from '@/Components/Button.vue';\nimport Input from '@/Components/Input.vue';\n\nexport default {\n    components: {\n        BreezeAuthenticatedLayout,\n        Head,\n        Checkbox,\n        Button,\n        Input\n    },\n    props: {\n        todos: {\n            type: [Array],\n        },\n    },\n    methods: {\n        submit() {\n            Inertia.post('/todo', {\"description\": this.newTodo});\n            this.newTodo = null;\n        },\n        check(checkboxInfo){\n            Inertia.post('/todo/' + checkboxInfo.id, {\"checked\": checkboxInfo.checked});\n        },\n        remove(id){\n            Inertia.delete('/todo/' + id);\n        }\n    },\n    data() {\n        return {\n            newTodo: null\n        }\n    }\n}\n</script>\n\n<style>\n    .done{\n        text-decoration: line-through;\n    }\n    table {\n        width: 100%;\n    }\n    table tr:nth-child(even) {\n        background: #f3f4f6;\n    }\n    table tr td:first-child{\n        width:0.1%;\n        white-space: nowrap;\n        padding-right: 20px;\n    }\n    table tr td:first-child input[type='checkbox']{\n        margin-right: 10px;\n    }\n    table i{\n        cursor: pointer;\n    }\n</style>\n"],"sourceRoot":""}]);
